:root {
  --main-color: #060b35;
  --secondary-color: #616070;
  --blue-color: #00acdc;
  --white-color: #fff;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main__catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.image {
  justify-content: center;
  align-items: center;
  display: flex;
}

.img {
  width: 160px;
  height: 134px;
}

.title {
  color: var(--main-color);
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.description {
  color: var(--secondary-color);
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.rating {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  margin-bottom: 26px;
  display: flex;
}

.rating__stars {
  display: flex;
}

.rating__reviews {
  color: var(--main-color);
  font-size: 10px;
  font-weight: 400;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__name {
  color: var(--secondary-color);
  font-size: 12px;
  font-weight: 400;
}

.price__num {
  color: var(--main-color);
  font-size: 16px;
  font-weight: 700;
}

.button {
  border: 1px solid var(--blue-color);
  letter-spacing: 0%;
  color: var(--white-color);
  text-transform: uppercase;
  background-color: var(--blue-color);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 18px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s, color .3s;
  display: flex;
}

.button:hover {
  cursor: pointer;
  color: var(--blue-color);
  background-color: var(--white-color);
}

.card:hover .title {
  color: #34568b;
}

.card:hover {
  transform: scale(1.2);
}

@media (width >= 488px) {
  .main__catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main__catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main__catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
